nav{
    background: rgba(0,0,0,0.3);
    width: max-content;  /** Learn this **/
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;  /** Learn this **/
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;  /** Can display flex, and display be appplied to one element, and what exactly does display flex do **/
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);  /** Learn this **/
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}

