header {
    height: 100vh; /*** Learn this stuff ***/
    padding-top: 7em;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*********************** CTA *****************/

.cta {
    margin-top: 2.5rem;
    display: flex; /********* Learn More ************/
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/*********************** HEADER SOCIALS *****************/
.header__socials{
    display: flex;
    flex-direction: column; /********* Learn More ************/
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{ /********* Learn More ************/
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*********************** ME *****************/

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem); /********* Learn More ************/
    /* transform: translateX(-50%); */ /********* Learn More ************/
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/*********************** SCROLL DOWN *****************/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/******************* MEDIA QUERIES (MEDIUM DEVICES) ************************/


@media screen and (max-width:1024px) {
    header{
        height: 68vh; /********* Learn More ************/
    }
}


/******************* MEDIA QUERIES (SMALL DEVICES) ************************/


@media screen and (max-width:600px) {
    header{
        height: 100vh; /********* Learn More ************/
    }

    .header__socials, .scroll__down{
        display: none;
    }
}



nav{
    background: rgba(0,0,0,0.3);
    width: -webkit-max-content;
    width: max-content;  /** Learn this **/
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;  /** Learn this **/
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;  /** Can display flex, and display be appplied to one element, and what exactly does display flex do **/
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);  /** Learn this **/
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}


.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1; /* Learn this */
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg) ;
            transform: rotate(10deg) ; /* Learn this */
    transition: var(--transition); /* Learn this */
}

.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default; /* Learn this */
}

.about__icons {
    color: var(--color-primary); /** can icons have a color? **/
    font-size: 1.4rem;
    margin-bottom: 1rem ;
}

.about__card h5{
    font-size: 0.95rem;
    color: var(--color-light)
}

.about__content{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/******************* MEDIA QUERIES (MEDIUM DEVICES) ************************/


@media screen and (max-width:1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}


/******************* MEDIA QUERIES (SMALL DEVICES) ************************/


@media screen and (max-width:600px) {

    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap :1rem;
        gap :1rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }

}
.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem; /** Learn this **/
}

.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details{
    margin-top: 6px;
    color: var(--color-primary);
}

.experience__details-icon{
    margin-top: 6px ;
    color: var(--color-primary);
}

/******************* MEDIA QUERIES (MEDIUM DEVICES) ************************/


@media screen and (max-width:1024px) {
   .experience__container{
    grid-template-columns: 1fr;
   }
   .experience__container > div{
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
   }

   .experience__content{
    padding: 1rem;
   }
}

/******************* MEDIA QUERIES (SMALL DEVICES) ************************/


@media screen and (max-width:600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}
.services__container{
    display: grid;
    grid-template-columns:repeat( 3, 1fr) ;
    grid-gap: 3rem;
    gap: 3rem;
}

.services{
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition:var(--transition);
}

.services:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service__head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list{
    padding: 2rem;
}

.service__list li{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service__list-icon{
    color: var(--color-primary);
    margin-top: 2px;
}

.service__list p{
    font-size: 0.9rem;
}

/******************* MEDIA QUERIES (MEDIUM DEVICES) ************************/


@media screen and (max-width:1024px) {
    .services__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }

    .services{
        height: auto; /** Learn this **/
    }
}

/******************* MEDIA QUERIES (SMALL DEVICES) ************************/


@media screen and (max-width:600px) {

    .services__container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}

